.shopDetail {
    display: flex;
    justify-content: space-between;
    padding: 12px 10px;
    width: 95%;
    border-radius: 5px;
    margin-top: 5px;
    font-size: 16px;
    font-weight: 700;
}

.bagCard {
    border: 1px solid rgb(214, 211, 211);
    padding: 12px;
    width: 94%;
    border-radius: 5px;
    margin-bottom: 8px;
}

/* .padding {
    padding: 10px;
} */

.productDiv {
    /* display: grid;
    grid-template-columns: 20% auto 20%; */
    display: flex;
    border-bottom: 1px solid rgb(214, 211, 211);
}

/* .productDiv > div {
    border: 2px solid red;
} */
.prodImage{
    width: 40%;
}
.bagProdInfo{
    display: flex;
    flex-direction: column;
}

.productDivSecond {
    text-align: left;
    font-size: 15px;
    /* padding-left: 142px; */
    padding-left: 14%;
    line-height: 1.5;
}

.productDivThird {
    display: flex;
    width: 100%;
    justify-content: space-evenly;

    text-align: right;
    font-size: 15px;
    padding-right: 10px;
    line-height: 1.5;
    padding-top: 10px;
}

.gridBtn {
    /* display: grid;
    grid-template-columns: 20% 50% auto; */
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding-top: 10px;
    color: rgb(65, 63, 63);
    font-weight: bold;
    font-size: 14px;
}

.removeDiv {
    border-right: 1px solid rgb(214, 211, 211);
}

.cursor {
    cursor: pointer;
}

.addMoreBox {
    border: 1px solid rgb(214, 211, 211);
    margin: 25px;
    width: 92.6%;
    border-radius: 5px;
    margin-bottom: 0px;
    margin-left: 0%;
    padding: 16px;
    padding-bottom: 6px;
    cursor: pointer;
    display: grid;
    grid-template-columns: 6% auto 10%;
    text-align: left;
    font-size: 15px;
    color: black;
}

.addMoreBox div:nth-child(2) {
    margin-top: -2px;
}

.addMoreBox div:nth-child(3) {
    text-align: right;
    margin-top: -2px;
}

