.gridModelBtn {
    display: grid;
    grid-template-columns: 60% auto;
    padding-top: 10px;
    color: rgb(65, 63, 63);
    font-weight: bold;
    font-size: 14px;
}

.bagModelCard {
    width: 430px;
    /* border: 1px solid red; */
    background-color: white;
    position: fixed;
    top: 0;
    margin-left: 240px;
    margin-top: 190px;
    padding: 15px;
    border-radius: 5px;
}

.flexBagModel {
    display: grid;
    grid-template-columns: 14% auto;
}

.bagModelBg {
    width: 150%;
    height: 100vh;
    background-color: rgb(177, 170, 170);
    opacity: 50%;
    position: fixed;
    top: 0;
    margin-left: -20%;
}

.closeBagModel {
    width: 23px;
    height: 23px;
    border: 1px solid grey;
    cursor: pointer;
    font-size: 22px;
    font-weight: 500;
    position: fixed;
    top: 0;
    margin-left: 660px;
    margin-top: 205px;
    border-radius: 50%;
    /* color: grey; */
}

.closeBagModel div {
    background: none;
    margin-top: -7px;
}

