  /* For Mobile Phones Portrait or Landscape View */
  @media screen and (max-width: 400px){
    .productDiv{
        flex-direction: column;
    }
    .prodImage{
        width: 100%;
    }
    .productDivSecond{
        padding-left: 0;
    }
    .productDivThird{
        justify-content: space-between;
    }
    .removeDiv   {
        width: 100%;
        border: 2px solid white;
        background-color: red;
        color: white;
    }
    .horPartition{
        width: 100%;
        margin-left: 0%;
    }
}

