@import url('https://fonts.googleapis.com/css2?family=Catamaran:wght@100;200;300;400;500;600;700;800;900&display=swap');



#main_wrapper {
    border: 0px solid red;
    padding: 30px 32px 50px 32px;
    /* display: grid;
    grid-template-columns: 18% 18% 18% 18% 18%;
    gap: 30px; */
    width: 75%;
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
}


.item_div {
    border: 0px solid red;
    height: 360px;
    z-index: -1;
}

.item_title {
    /* text-align: left;
    font-family: 'Catamaran', sans-serif;
    margin-left: 10px;
    font-weight: 700;
    color: #1f2233; */
    text-align: left;
    font-size: 16px;
    font-weight: 500;
    line-height: 1;
    color: #282c3f;
    margin-bottom: 6px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-top: 6px;
    margin-left: 10px;
}

.item_sub_heading {
    /* border: 0px solid red;
    height: 17px;
    text-align: left;
    margin-left: 10px;
    font-family: 'Catamaran', sans-serif;
    line-height: 16px;
    overflow-y: hidden;
    font-size: 15px;
    color: #7E818C;
    margin-top: -2px; */
    text-align: left;
    color: #535766;
    font-size: 14px;
    line-height: 1;
    margin-bottom: 0;
    margin-top: 0;
    margin-left: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-weight: 400;
    display: block;
}

.tag_wrapper {
    width: 100px;
    height: 80px;
    position: absolute;
    margin-top: -280px;
    border: 0px solid red;
    background: none;
}


/* .tag_wrapper > div {
    background: none;
    margin-top: -px;
    border: 2px solid blue;
} */

.tag_wrapper > img {
    width: 100%;
    border: 0px solid red;
    background: none;
}

.tag_wrapper > div {
    position: absolute;
    color: white;
    font-family: 'Catamaran', sans-serif;
    font-size: 12px;
    width: 120px;
    font-weight: 700;
    margin-top: -28px;
    margin-left: -16px;
    background: none;
}

.item_image {
    width: 100%;
}

.item_price_div {
    display: flex;
    margin-left: 10px;
    gap: 10px;
}

.item_price_div > div:nth-child(1) {
    font-family: 'Catamaran', sans-serif;
    font-weight: 700;
    font-size: 14px;
    color: #1f2233;
}

.item_price_div > div:nth-child(2) {
    font-family: 'Catamaran', sans-serif;
    font-size: 11px;
    margin-top: 5px;
    color: #4a4e5a;
    text-decoration: line-through;
}

.item_price_div > div:nth-child(3) {
    font-family: 'Catamaran', sans-serif;
    font-size: 11px;
    margin-top: 5px;
    color: red;
}

.price {
    text-align: left;
    margin-left: 10px;
    font-family: 'Catamaran', sans-serif;
    font-weight: 700;
    font-size: 14px;
    color: #1f2233;
}

.dots {
    background-color: white;
    border: 0px solid red;
    box-sizing: border-box;
    padding-top: 5px;
    padding-left: 41px;
    padding-right: 41px;
    width: 150px;
    margin: auto;
    margin-top: 2px;
    display: flex;
    gap: 15px;
}

.dots_div {
    width: 6px;
    height: 6px;
    background-color: #D4D5D9;
    border-radius: 50%;
}

.dots_div2 {
    width: 6px;
    height: 6px;
    background-color: #FF8080;
    border-radius: 50%;
}
.productBase{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-align: stretch;
    -ms-flex-align: stretch;
    align-items: stretch;
    -ms-flex-line-pack: stretch;
    align-content: stretch;
    margin: 0 -10px 0 3px;
    width: 100%;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}
/* For Mobile Phones Portrait or Landscape View */
@media screen and (max-width: 400px){
.link{
    width: 115px !important;
}
}
.link {
    text-decoration: none;
    /* width: 21%;
    padding: 15px; */

    width: 210px;
    text-align: left;
    position: relative;
    vertical-align: top;
    overflow: hidden;
    display: inline-block;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0 10px 30px;
}
.link:hover{
    /* height: 200px;
    width: 200px;
    z-index: 2;
    margin-right: 80px; */
}
.wishlist_button {
    display: flex;
    border: 1px solid #A9ABB3;
    /* width: 170px; */
    width: 90%;
    height: 35px;
    margin: auto;
    margin-top: 5px;
    border-radius: 0.1em;
    box-sizing: border-box;
    padding: 10px;
    margin-bottom: 10px;
    cursor: pointer;
}

.wishlist_button:hover {
    border: 1px solid black;
}

.wishlist_text {
    margin-top: -5px;
    font-family: 'Catamaran', sans-serif;
    font-weight: 800;
    color: #1f2233;
    font-size: 14px;
    /* margin-top: 1px; */
    margin-left: 5px;
}

.sizes {
    text-align: left;
    margin-left: 11px;
    font-family: 'Catamaran', sans-serif;
    font-size: 14px;
    color: #535665;
    
}

.sizes > span {
    font-family: 'Catamaran', sans-serif;
    font-size: 16px;
    color: #A9ABB3;
}

.hover_div {
    position: absolute;
    background-color: #FFFFFF;
     /* width: 190px;  */
     width: 210px;
    display: none;
    margin-top: -35px;
    box-shadow: 1px 0px 4px grey;
    z-index: 50;
    /* left: 246px; */
}

.non_hover_div {
    display: block;
}

.item_div:hover .non_hover_div {
    display: none;
}

.item_div:hover .hover_div{
    display: block;
}

.pagination_wrapper {
    border: 0px solid red;
    padding: 25px 0px 70px 0px;
    /* grid-column: 1 / 6; */
    /* display: flex;
    gap: 440px; */
    /* display: grid;
    grid-template-columns: 25% 75%; */
    border-top: 1px solid #d2cbd8;
    margin-top: 45px;
    display: flex;
    justify-content: space-between;

}

.sub_pagi_wrapper{
    display: flex;
    /* gap: 170px; */
}

.visi_hidden {
    visibility: hidden;
}

.sub_pagi_wrapper > div:nth-child(1), .sub_pagi_wrapper > div:nth-child(3) {
    border: 1px solid #93959F;
    box-sizing: border-box;
    padding: 0px 25px 0px 25px;
    border-radius: 0.3em;
    cursor: pointer;
    height: 43px;
}

.sub_pagi_wrapper > div:nth-child(1):hover, .sub_pagi_wrapper > div:nth-child(3):hover {
    border: 1px solid black;
}


.pagi_number{
    display: flex;
    gap: 15px;
    font-family: 'Catamaran', sans-serif;
    font-size: 20px;
    margin-top: 5px;
}

.pagi_number > div {
    border: 0px solid red;
    padding: 0px 10px 0px 10px;
    height: 35px;
    border-radius: 0.2em;
    cursor: pointer;
}

.pagi_number > div:hover { 
    border: 1px solid #bab4c0;
}

.bg_color {
    background-color: #282C3F;
    color: white;
}


.pagination_wrapper > div {
    font-family: 'Catamaran', sans-serif;
    font-size: 18px;
    font-weight: 700;
    /* margin-top: -5px; */
    border: 0px solid green;
    width: 70%;
}

.pagination_wrapper > div:nth-child(1) {
    text-align: left;
    font-family: 'Catamaran', sans-serif;
    font-size: 15px;
    font-weight: 300;
    margin-top: 9px;
}

.pagination_wrapper span {
    font-family: 'Catamaran', sans-serif;
    font-size: 25px;
    margin-top: -2px;
}

.rating_wrapper {
    display: flex;
    position: absolute;
    background-color: white;
    border: 0px solid red;
    padding: 1px 5px 1px 5px;
    margin-top: -35px;
    margin-left: 7px;
    opacity: 80%;
    font-family: 'Catamaran', sans-serif;
    font-size: 13px;
    color: #282C3F;
    font-weight: 800;
    border-radius: 0.3em;
}

.rating_image {
    width: 20px;
    height: 20px;
}